@-webkit-keyframes effect-scale {
    50% {
        transform: scale(1.4, 1.4);
        opacity: 0.7;
    }
    95% {
        transform: scale(0.01, 0.01);
        opacity: 0;
    }
    100% {
        transform: scale(1, 1);
        opacity: 1;
    }
}

@keyframes effect-scale {
    50% {
        transform: scale(1.4, 1.4);
        opacity: 0.7;
    }
    95% {
        transform: scale(0.01, 0.01);
        opacity: 0;
    }
    100% {
        transform: scale(1, 1);
        opacity: 1;
    }
}

@-webkit-keyframes effect-scale1 {
    0% {
        transform: scale(0.001, 0.001);
        opacity: 0;
    }
    20% {
        transform: scale(1, 1);
        opacity: 0.85;
    }
    80% {
        transform: scale(1, 1);
        opacity: 0.85;
    }
    100% {
        transform: scale(0.001, 0.001);
        opacity: 0;
    }
}

@keyframes effect-scale1 {
    0% {
        transform: scale(0.001, 0.001);
        opacity: 0;
    }
    20% {
        transform: scale(1, 1);
        opacity: 0.85;
    }
    80% {
        transform: scale(1, 1);
        opacity: 0.85;
    }
    100% {
        transform: scale(0.001, 0.001);
        opacity: 0;
    }
}
.modal-post {
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 11;
}

.modal-post .modal__overlay {
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,.65);
    position: absolute;
    z-index: 11;
}

.modal-post ul {
    width: 400px;
    border: #555 1px solid;
    background-color: #fff;
    text-align: center;
    border-radius: 10px;
    z-index: 12;
    -webkit-animation: show-modal 0.15s ease-out;
            animation: show-modal 0.15s ease-out;
}

.modal-post ul li {
    border-bottom: 1px solid #dbdbdb;
    line-height: 48px;
    font-size: 15px;
    color: #262626;
    cursor: pointer;
}

.modal-post .header__message {
    font-size: 22px;
    font-weight: 700;
    padding: 20px 0 5px;
    color: #000;
    cursor: default;
}

.modal-post .body__message {
    margin: 0;
    line-height: 30px;
    padding: 0 30px 20px;
}

.modal-post ul li:last-child {
    border-bottom: none;
}

.modal-post .warnning {
    color: rgb(237,73,86);
    font-weight: 700;
}

.modal-post .bo-theo-doi {
    color: rgb(237,73,86);
    font-weight: 700;
}

.modal-post .theo-doi {
    color: #0095f6;
    font-weight: 700;
}

